body {
    margin: 20px;
    font-family: Arial, sans-serif;
    background-color: #161616;
    color: #333;
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

h1 {
    color: #D9D9D9;
}
h2 {
    color: #FF7C48;
    font-size: 2rem;
}
h3 {
    color: #D9D9D9;
    font-size: 2rem;
}
p{
    color: #D9D9D9;
    padding-right: 4rem;
    font-size: 18px;
}

.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
}

.logo img {
    height: 40px;
}

.nav-links {
    list-style: none;
    display: flex;
    gap: 20px;
    margin: 0;
    padding: 0;
}

.nav-links li {
    display: inline;
}

.nav-links a {
    text-decoration: none;
    color: #D9D9D9;
    font-size: 16px;
    padding: 8px 12px;
    transition: background 0.3s;
}

.nav-links a:hover {
    border-radius: 5px;
    color: #FF7C48;
}
.nav-links a.active {
    color: #FF7C48;
}

.header {
    text-align: center;
    padding: 70px 20px;
    background-color: #161616;
    color: white;
    position: relative;
    max-width: 80%;
    margin: 0 auto;
}

/* Title Styling & Animation */
.main-title {
    font-size: 3rem;
    font-weight: bold;
    margin: 70px 0;
    color: #D9D9D9;
    position: relative;
    z-index: 2;
    opacity: 0;
    animation: fadeIn 1.5s ease-out forwards 0.5s;
}

/* Quote Container */
.quote-container {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 70px;
    position: relative;
}

.quote {
    font-size: 1rem;
    font-style: italic;
    color: #bbb;
    position: relative;
    opacity: 0;
    transform: scale(0.9);
    transition: transform 0.3s ease-in-out;
}

/* Hover Effect */
.quote:hover {
    transform: scale(1.1);
    color: #D9D9D9;
}


/* Quote Arrangement (Positioning Remains) */
.quote-top-left { transform: translate(-100px, -15px); animation: fadeIn 1s ease-out forwards 1s; }
.quote-top-center { transform: translate(0px, 5px); animation: fadeIn 1s ease-out forwards 1.5s; }
.quote-top-right { transform: translate(100px, -15px); animation: fadeIn 1s ease-out forwards 2s; }

.quote-bottom-left { transform: translate(-100px, 15px); animation: fadeIn 1s ease-out forwards 2.5s; }
.quote-bottom-center { transform: translate(0px, -5px); animation: fadeIn 1s ease-out forwards 3s; }
.quote-bottom-right { transform: translate(100px, 15px); animation: fadeIn 1s ease-out forwards 3.5s; }

/* Keyframe Animations */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
    }
}


.main {
    padding: 2rem;
    flex-grow: 1;
}

.concept-content {
    display: flex;
    flex-direction: row;  /* Zorg ervoor dat de tekst en afbeelding naast elkaar staan */
    gap: 80px;  /* Ruimte tussen de tekst en de afbeelding */
    align-items: flex-start;  /* Zorgt ervoor dat ze bovenaan uitgelijnd zijn */
    margin-bottom: 50px;
}

.text-container {
    flex: 1;  /* Zorgt ervoor dat de tekst ruimte inneemt */
}

.image-container {
    flex-shrink: 0;  /* Zorg ervoor dat de afbeelding niet krimpt */
}
.item-tekst{
    width: 33%;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;  /* Center text horizontally */
    height: 200px;            /* Fixed height matching the images */
    padding: 20px;
    box-sizing: border-box;
    box-shadow: 0 2px 6px 2px #d9d9d917;
    border-radius: 5px;
}
.image-container img {
    max-width: 80%;  /* De afbeelding vult de container */
    height: 200px;        /* Fixed height */
    object-fit: cover;     /* Crop the image if necessary */
}

.action-button {
    display: inline-block;  
    margin-top: 40px;  
    padding: 12px 24px;  /* Adjusted padding for better proportions */
    background-color: #111;  /* White background */
    color: #FF7C48;  /* Orange text */
    border: 2px solid #FF7C48;  /* Orange border */
    border-radius: 50px;  /* Large value to make it fully rounded */
    font-size: 1rem;  
    cursor: pointer;  
    text-align: center;  
    transition: background-color 0.3s, color 0.3s;  /* Smooth effect */
}

/* Hover effect */
.action-button:hover {
    background-color: #FF7C48;  /* Orange background on hover */
    color: white;  /* White text on hover */
}


.stories {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 1rem;
}

.story-card {
    background-color: #222;
    padding: 1rem;
    border-radius: 8px;
    box-shadow: 2px 2px 10px rgba(255, 255, 255, 0.1);
}

button {
    background-color: gray;
    color: white;
    padding: 10px;
    border: none;
    cursor: pointer;
    margin-top: 10px;
}

button:hover {
    background-color: darkgray;
}

.footer {
    text-align: center;
    padding: 1rem;
    color: gray;
}

.container {
    max-width: 100%;
    margin: auto;
    background: #161616;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.gedichten-container {
    margin-top: 20px;
    text-align: center;
}

.gedichten-grid {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.gedichten-row {
    display: flex;
    gap: 10px;
    justify-content: center;
    width: 100%;
    max-width: 100%; /* Adjust for your design */
}

.gedicht {
    background: #222;
    padding: 15px;
    border-radius: 5px;
    text-align: left;
    flex: 1;
    color: white;
    max-width: 100%;
}

.gedicht p {
    font-size: 16px;
    line-height: 1.5;
}

.gedicht small {
    display: block;
    margin-top: 10px;
    color: #aaa;
}

.gedicht-img {
    width: 100%;
    max-width: 100%;
    height: auto;
    border-radius: 5px;
}

/*.image-wrapper {
    width: 100%;
    height: 100%;
    overflow: hidden;
}*/
.gedicht-img.full-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
    max-width: none; /* ensure full width coverage */
}

/* Container voor het geheel */
.container-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 20px;
}


.image-text-grid {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin: 2rem auto; /* Reduced top margin to bring it closer to "Stories from the inside" */
}
.image-text-grid .item {
    width: 30%;
    text-align: left;
}
.image-text-grid .item img {
    width: 100%;
    height: 200px;         /* Fixed height for uniformity */
    object-fit: cover;      /* Crop image to fill the container */
    display: block;
    border-radius: 8px;
    align-items: center;
}

.image-container {
    flex-shrink: 0;  /* Zorg ervoor dat de afbeelding niet krimpt */
}

/* Menu sectie */
.footer {
    display: flex;
    justify-content: space-between;
    width: 100%;
    border-top: 2px solid #FF7C48;

}

/* Logo */
.logo img {
    width: 150px;  /* Pas dit aan naar de grootte van je logo */
    margin-bottom: 20px;
}

/* Pagina links */
.footer-links ul {
    list-style: none;
}

.footer-links ul li {
    margin: 10px 0;
}

.footer-links ul li a {
    text-decoration: none;
    color: white;
    font-size: 18px;
    transition: color 0.3s;
}

.footer-links ul li a:hover {
    color: #FF7C48;
}

/* Social media icons */
.social-icons {
    display: flex;
    gap: 15px;
    margin-top: 20px;
}

.social-icons a {
    font-size: 30px; /* Zet de grootte van de iconen */
    color: white; /* Zet de kleur van de iconen naar wit */
    transition: transform 0.3s;
    text-decoration: none; /* Verwijder onderlijning */
}

/* Hover effect voor de iconen */
.social-icons a:hover {
    transform: scale(1.1);
}


/* Copyright sectie */
.copy {
    margin-top: 30px;
    text-align: center;
}

.copy p {
    font-size: 14px;
    color: #777;
}

.scroll-down {
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 30px;
    color: #FF7C48;
    cursor: pointer;
    animation: bounce 2s infinite;
}

@keyframes bounce {
    0%, 100% {
        transform: translate(-50%, 0);
    }
    50% {
        transform: translate(-50%, 10px);
    }
}


.stories-header {
    display: flex;
    align-items: center;
    gap: 15px;  /* Space between title and button */
}

.more-stories-button {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #FF7C48;  /* Orange text */
    font-size: 1rem;
    font-weight: normal;
    padding: 8px 16px;
    border-radius: 50px;  /* Rounded button */
    overflow: hidden;
    transition: all 0.3s ease-in-out;
    position: relative;
    white-space: nowrap;
}

.more-stories-button .text {
    opacity: 1;
    transition: opacity 0.3s;
}

.more-stories-button .arrow {
    opacity: 0;  /* Initially hidden */
    margin-left: 10px;  /* Space between text and arrow */
    transition: opacity 0.3s, transform 0.3s ease;
    font-size: 1.2rem;
}

.more-stories-button:hover .arrow {
    opacity: 1;  /* Arrow appears on hover */
    transform: translateX(5px);  /* Arrow moves on hover */
}

/* Small screen adjustments (phones) */
@media (max-width: 480px) {
    .header {
      max-width: 95%;
      padding: 40px 10px;
    }
    
    .main-title {
      font-size: 2rem;
      margin: 30px 0;
    }
    
    p {
      padding-right: 0;
      font-size: 16px;
    }
    
    .action-button {
      width: 100%;
      box-sizing: border-box;
    }
  }
  
  /* Responsive tweaks for tablets and smaller */
  @media (max-width: 1024px) {
    .concept-content {
      flex-direction: column;
      gap: 40px;
      text-align: left;
    }
    
    .text-container,
    .image-container {
      width: 100%;
      margin: 0 auto;
    }
    
    .image-text-grid {
      flex-direction: column;
      align-items: center;
    }
    
    .image-text-grid .item {
      width: 80%;
    }
    
    .stories-header {
      flex-direction: column;
      align-items: flex-start;
    }
    
    /* Quote adjustments for tablets */
    .quote-container {
      gap: 40px;
    }
    
    .quote-top-left, 
    .quote-top-right, 
    .quote-bottom-left, 
    .quote-bottom-right {
      transform: translate(0, 0);
    }
  }
  
  /* Mobile styles */
  @media (max-width: 768px) {
    .nav {
      flex-direction: column;
      align-items: flex-start;
    }
    
    .nav-links {
      flex-direction: column;
      gap: 10px;
      width: 100%;
      padding-top: 10px;
    }
    
    .container-footer,
    .footer {
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
    
    .footer-links ul {
      padding: 0;
    }
    
    .quote-container {
      flex-direction: column;
      gap: 30px;
    }
    
    .quote {
      transform: none !important;
      animation-delay: 0.5s !important;
    }
    
    .gedichten-row {
      flex-direction: column;
      align-items: center;
    }
    
    .gedicht {
      width: 90%;
    }
    
    .stories {
      grid-template-columns: 1fr;
    }
    
    .scroll-down {
      font-size: 24px;
      bottom: 10px;
    }
    
    .main {
      padding: 1.5rem;
    }
    
    .image-container img {
      max-width: 100%;
    }
    
    h2 {
      font-size: 1.8rem;
    }
    
    h3 {
      font-size: 1.6rem;
    }
  }
  
  /* Extra adjustments for very small screens */
  @media (max-width: 360px) {
    .nav-links a {
      padding: 6px 8px;
      font-size: 14px;
    }
    
    .main-title {
      font-size: 1.7rem;
    }
    
    .action-button {
      padding: 10px 20px;
      font-size: 0.9rem;
    }
  }

 /* Update your nav class to maintain proper justification */
.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    position: relative; /* Ensure positioning context */
}

/* Logo styling - keep as is */
.logo img {
    height: 40px;
}

/* Hamburger Menu Styles - update for positioning */
.hamburger {
    display: none;
    flex-direction: column;
    justify-content: space-between;
    width: 30px;
    height: 21px;
    cursor: pointer;
    z-index: 20;
}

.hamburger span {
    display: block;
    height: 3px;
    width: 100%;
    background-color: #FF7C48;
    border-radius: 3px;
    transition: all 0.3s ease;
}

/* Responsive Navigation */
@media (max-width: 768px) {
    .hamburger {
        display: flex;
        position: absolute; /* Position absolutely */
        right: 20px; /* Align to the right edge with some padding */
        top: 50%; /* Center vertically */
        transform: translateY(-50%); /* Perfect vertical centering */
        z-index: 20;
    }
    
    .nav {
        padding: 15px 20px;
        position: relative;
        flex-direction: row; /* Keep as row even in mobile */
        justify-content: flex-start; /* Align logo to the left */
    }
    
    .nav-links {
        position: fixed;
        top: 0;
        right: -100%;
        width: 70%;
        height: 100vh;
        background-color: #222;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 30px;
        transition: right 0.4s ease;
        z-index: 10;
        padding: 80px 0;
        box-shadow: -5px 0 15px rgba(0, 0, 0, 0.3);
    }
    
    .nav-links.active {
        right: 0;
    }
    
    .nav-links li {
        width: 100%;
        text-align: center;
    }
    
    .nav-links a {
        display: block;
        padding: 15px;
        font-size: 18px;
    }
    
    /* Hamburger menu animation */
    .hamburger.active span:nth-child(1) {
        transform: translateY(9px) rotate(45deg);
    }
    
    .hamburger.active span:nth-child(2) {
        opacity: 0;
    }
    
    .hamburger.active span:nth-child(3) {
        transform: translateY(-9px) rotate(-45deg);
    }
    
    /* Add overlay when menu is open */
    .overlay {
        display: none;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5);
        z-index: 5;
    }
    
    .overlay.active {
        display: block;
    }
}